/* eslint-disable no-return-assign */
/* eslint-disable no-undef */
import React from 'react'
// import moment from 'moment'
// import { render } from 'react-dom'
import axios from 'axios'
import { Table, Button, message } from 'antd'
// import './mock/index.js'
import { RenderForm } from 'dist/main.js'
// import { RenderForm } from '../../lib'
console.log(React)
const baseUrl = 'http://localhost:8091/api'

class FormApp extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      list: [],
    }
  }

  componentDidMount () {
    this.getUserData()
  }

  getUserData = (params) => {
    console.log('params', params)
    axios({ url: `${baseUrl}/getFormData`, method: 'get', params }).then((res) => {
      this.setState({ list: res.data.data })
    })
  }

  // eslint-disable-next-line class-methods-use-this
  get baseFormProps () {
    return {
      title: '表单',
      // view: true,
      searchColums: [
        {
          label: 'hidden',
          dataIndex: 'name',
          searchType: 'input',
          // hidden: true,
          // initialValue: '1111',
          // rules: [
          //   {
          //     required: true,
          //     message: 'name',
          //   },
          // ],
          formItemProps: {

            extra: '1111111111111111111'
          },
          props: {
            // readOnly: true,
            placeholder: 'name',
          },
        },
        {
          label: 'createTime',
          dataIndex: 'createTime',
          searchType: 'datePicker',
          // initialValue: moment('2015-01-01'),
          // rules: [
          //   {
          //     required: true,
          //     message: 'name',
          //   },
          // ],
          props: {
            // readd
            format: 'YYYY/MM/DD',
            placeholder: 'createTime',
          },
        },
        {
          label: 'nickName',
          dataIndex: 'nickName',
          searchKey: 'email',
          searchType: 'inputEnterSearch',
          initialValue: 'inputEnterSearch',
          // enterButtonSearch: (callback, childInputs) => {
          //   if (1) {
          //     message.info('请选择创建时间')
          //   }
          // },
          modalProps: {
            tableRowKey: 'id',
            tableColums: [
              { dataIndex: 'id', key: 'id', title: 'ID' },
              { dataIndex: 'name', key: 'name', title: 'name' },
              { dataIndex: 'nickName', key: 'nickName', title: 'nickName' },
              { dataIndex: 'phone', key: 'phone', title: 'phone' },
              { dataIndex: 'age', key: 'age', title: 'age' },
              {
                dataIndex: 'isMale', key: 'isMale', title: 'isMale', render: (text) => (text ? '男' : '女'),
              },
              { dataIndex: 'email', key: 'email', title: 'email' },
              { dataIndex: 'createTime', key: 'createTime', title: 'createTime' },
            ],
            searchColums: [
              {
                label: 'name',
                dataIndex: 'name',
                searchType: 'input',
                initialValue: '',
                props: {
                  placeholder: 'name',
                },
              },
            ],
            fetchData: () => {
              return (params, callback) => {
                axios({ url: `${baseUrl}/getFormData`, method: 'get', params }).then((res) => {
                  console.log('res.data', res.data)
                  callback(res.data)
                })
                // dispatch({ type: '/query', callback })
              }
            },
            // onModalOk: (callback, oldSelectItem, newSelectItem) => {
            //   if (1) {
            //     message.info('请选择创建时间')
            //   }
            // },
          },
        },
        {
          label: 'isMale',
          dataIndex: 'isMale',
          searchType: 'select',
          initialValue: 0,
          selectOptions: [
            { value: 0, text: '男' },
            { value: 1, text: '女' },
          ],
        },
      ],
      // eslint-disable-next-line no-unused-vars
      handleSearch: false,
      handleResetSearch: false,
      // 灵活布局表单样式
      otherProps: {
        formItemLayout: {
          labelCol: { span: 3 },
          wrapperCol: { span: 6 },
        },
        listProps: {
          xs: { span: 24 },
          sm: { span: 24 },
          md: { span: 24 },
          lg: { span: 24 },
          xl: { span: 24 },
        },
      },
    }
  }

  get baseFormProps1 () {
    return {
      // title: '表单',
      // view: true,
      searchColums: [
        {
          label: 'hidden',
          dataIndex: 'name1',
          searchType: 'input',
          hidden: true,
          // initialValue: '1111',
          // rules: [
          //   {
          //     required: true,
          //     message: 'name',
          //   },
          // ],
          props: {
            // readOnly: true,
            placeholder: 'name',
          },
        },
        {
          label: 'createTime1',
          dataIndex: 'createTime1',
          searchType: 'datePicker',
          // initialValue: moment('2015-01-01'),
          // rules: [
          //   {
          //     required: true,
          //     message: 'name',
          //   },
          // ],
          props: {
            // readd
            format: 'YYYY/MM/DD',
            placeholder: 'createTime',
          },
        },
        {
          label: 'sadasas',
          dataIndex: 'nickName',
          searchKey: 'email',
          searchType: 'inputEnterSearch',
          initialValue: '',
          // enterButtonSearch: (callback, childInputs) => {
          //   // if (0) {
          //   //   message.info('请选择创建时间')
          //   // }
          // },
          modalProps: {
            title: '选择111',
            tableRowKey: 'id',
            tableColums: [
              { dataIndex: 'id', key: 'id', title: 'ID' },
              { dataIndex: 'name', key: 'name', title: 'name' },
              { dataIndex: 'nickName', key: 'nickName', title: 'nickName' },
              { dataIndex: 'phone', key: 'phone', title: 'phone' },
              { dataIndex: 'age', key: 'age', title: 'age' },
              {
                dataIndex: 'isMale', key: 'isMale', title: 'isMale', render: (text) => (text ? '男' : '女'),
              },
              { dataIndex: 'email', key: 'email', title: 'email' },
              { dataIndex: 'createTime', key: 'createTime', title: 'createTime' },
            ],
            searchColums: [
              {
                label: 'name',
                dataIndex: 'name',
                searchType: 'input',
                initialValue: '',
                props: {
                  placeholder: 'name',
                },
              },
            ],
            fetchData: () => {
              return (params, callback) => {
                axios({ url: `${baseUrl}/getFormData`, method: 'get', params }).then((res) => {
                  callback(res.data)
                })
                // dispatch({ type: 'query', callback })
              }
            },
            // onModalOk: (callback, oldSelectItem, newSelectItem) => {
            //   if (1) {
            //     message.info('请选择创建时间')
            //   }
            // },
          },
        },
        {
          label: 'isMale1',
          dataIndex: 'isMale',
          searchType: 'select',
          initialValue: 0,
          selectOptions: [
            { value: 0, text: '男' },
            { value: 1, text: '女' },
          ],
        },
      ],
      // eslint-disable-next-line no-unused-vars
      handleSearch: ({ err, values }) => {
        console.log(this)
        this.getUserData(values)
      },
      footerBtns: [
        <Button type="primary"
          onClick={() => {
            let obj = {}
            // eslint-disable-next-line no-unused-vars
            this.table.handleInnerSearch(({ err, values }) => {
              obj = {
                ...obj,
                ...values,
              }
            })
            // eslint-disable-next-line no-unused-vars
            this.table1.handleInnerSearch(({ err, values }) => {
              obj = {
                ...obj,
                ...values,
              }
            })
            console.log(obj)
          }}
        >
          返回
        </Button>,
      ],
      handleSearchText: '保存',
      // handleResetSearch: false,
      // 灵活布局表单样式
      otherProps: {
        formItemLayout: {
          labelCol: { span: 3 },
          wrapperCol: { span: 6 },
        },
        listProps: {
          xs: { span: 24 },
          sm: { span: 24 },
          md: { span: 24 },
          lg: { span: 24 },
          xl: { span: 24 },
        },
      },
    }
  }

  render () {
    console.log(this.table)
    return (
      <div>
        <RenderForm getChild={(form) => this.table = form} {...this.baseFormProps} />
        <RenderForm getChild={(form) => this.table1 = form} {...this.baseFormProps1} />
        <Table
          bordered
          rowKey={(record) => record.id}
          columns={[
            { dataIndex: 'id', key: 'id', title: 'ID' },
            { dataIndex: 'name', key: 'name', title: 'name' },
            { dataIndex: 'nickName', key: 'nickName', title: 'nickName' },
            { dataIndex: 'phone', key: 'phone', title: 'phone' },
            { dataIndex: 'age', key: 'age', title: 'age' },
            {
              dataIndex: 'isMale', key: 'isMale', title: 'isMale', render: (text) => (text ? '男' : '女'),
            },
            { dataIndex: 'email', key: 'email', title: 'email' },
            { dataIndex: 'createTime', key: 'createTime', title: 'createTime' },
          ]}
          dataSource={this.state.list}
        />
      </div>
    )
  }
}

render(<FormApp />, document.getElementById('root'))
